#reading-progress-bar {
	appearance: none;
	position: fixed;
	bottom: 0;
	left: 0;
	display: block;
	width: 100%;
	height: 20px;
	background: none;
	z-index: 1000;
}

#reading-progress-bar::-webkit-progress-bar {
	background: transparent;
}

#reading-progress-bar::-webkit-progress-value {
	display: block;
	background-color: rgba(253, 216, 53, 0.5);
	border-radius: 0 10px 10px 0;
	transition: background-color 500ms;
}

#reading-progress-bar[value="0"]::-webkit-progress-value,
#reading-progress-bar[value^="0."]::-webkit-progress-value,
#reading-progress-bar[value^="1"]::-webkit-progress-value,
#reading-progress-bar[value^="2"]::-webkit-progress-value,
#reading-progress-bar[value^="3"]::-webkit-progress-value,
#reading-progress-bar[value^="4"]::-webkit-progress-value,
#reading-progress-bar[value^="5."]::-webkit-progress-value,
#reading-progress-bar[value^="6."]::-webkit-progress-value,
#reading-progress-bar[value^="7."]::-webkit-progress-value,
#reading-progress-bar[value^="8."]::-webkit-progress-value,
#reading-progress-bar[value^="9."]::-webkit-progress-value {
	background-color: rgba(96, 125, 139, 0.5);
}

#reading-progress-bar[value^="97."]::-webkit-progress-value,
#reading-progress-bar[value^="98."]::-webkit-progress-value,
#reading-progress-bar[value^="99."]::-webkit-progress-value,
#reading-progress-bar[value="100"]::-webkit-progress-value {
	background-color: rgba(67, 160, 71, 0.5);
}
