* {
	outline: none !important;
}

html,
body {
	height: 100% !important;
	overscroll-behavior-y: none !important;
}

body {
	margin: 0;
	-webkit-tap-highlight-color: transparent !important;
	-webkit-font-smoothing: antialiased !important;
	-moz-osx-font-smoothing: grayscale !important;

	/* self-defined */
	overflow-y: scroll;
	background-color: #b3e5fc !important;
}

body::before {
	content: "";
	display: block;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	z-index: -1000;
	background: linear-gradient(to bottom, #ffcdd2, #b3e5fc) no-repeat center center;
	background-size: cover;
}

button,
br,
img,
::placeholder,
a:not(.allow-select) {
	-webkit-touch-callout: none;
	-webkit-user-select: none;
	-khtml-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;

	-webkit-user-drag: none;
	-khtml-user-drag: none;
	-moz-user-drag: none;
	-ms-user-drag: none;
	user-drag: none;
}

img {
	image-orientation: from-image !important;
}

code,
.code {
	font-family: monospace;
}

::selection {
	color: white !important;
	background: #e91e63 !important;
	-webkit-text-fill-color: white !important;
}

::-moz-selection {
	color: white !important;
	background: #e91e63 !important;
	-webkit-text-fill-color: white !important;
}

::-webkit-scrollbar {
	width: 12px;
}

::-webkit-scrollbar-track {
	background: linear-gradient(to bottom, #f8bbd0, #b3e5fc);
}

::-webkit-scrollbar-thumb {
	background: linear-gradient(to bottom, #f06292, #ba68c8);
}

::-webkit-scrollbar-thumb:hover {
	background: linear-gradient(to bottom, #e91e63, #7b1fa2);
}

.particles-wrapper {
	position: fixed !important;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	z-index: -1;
}
